<template>
    <v-tooltip bottom>
        <template v-slot:activator="{ on }">
            <v-btn icon v-on="on" @click="generate"><v-icon color="red">mdi-file-pdf</v-icon></v-btn>
        </template>
        <span>Generar PDF</span>
    </v-tooltip>
</template>

<script>
    import {mapState} from 'vuex'
    let pdfMake = require("pdfmake/build/pdfmake.js");
    let pdfFonts = require("pdfmake/build/vfs_fonts.js");
    pdfMake.vfs = pdfFonts.pdfMake.vfs;
    export default {
        name: "SIDPDFMaker",
        props: [

        ],
        data() {
            return {
                today: new Date(),
                docDefinition: null,
                pageSize: 'LETTER',
                pageOrientation: 'landscape',
                pageMargins: [ 40, 100, 40, 60 ],
                styles: {
                    title: {fontSize: 20, bold: true},
                    subtitle: {bold: true, fontSize: 16},
                    bodyText: {fontSize: 12},
                    table: {fontSize: 6},
                    footer: {fontSize: 10, italics: true, color: 'gray'}
                },
                icon: 'data:image/png;base64,',
                bodyTable: [],
            };
        },
        computed: {
          ...mapState(['services','tracingData','optionPanelProperties'])
        },
        methods: {
            makeDesagregadoReport() {
                this.makeTableBody();
                this.docDefinition = {
                    pageSize: this.pageSize,
                    pageOrientation: this.pageOrientation,
                    pageMargins: this.pageMargins,
                    styles: this.styles,
                    header: [
                        {
                            columns: [
                                {stack: [
                                        'FUNDACIÓN PANAMERICANA PARA EL DESARROLLO',
                                        {text: 'Reporte por región', style: 'subtitle'},
                                        {text: 'Fecha: ' + this.getCurrentDate(), style: 'bodyText'},
                                    ],
                                    style: 'title', margin: [ 40, 30, 0, 0 ]},
                                {image: this.icon, width: 140, height: 50, alignment: 'right', margin: [ 0, 30, 20, 30 ]},
                            ],
                        },
                    ],
                    content: [
                        {
                            style: 'table',
                            table: {
                                widths: [ '*' ],
                                body: this.bodyTable
                            }
                        },
                    ],
                    footer: (currentPage, pageCount) => {
                        return {
                            columns: [
                                {text: 'Reporte generado por: ' + window.User.Nombre + ' ' + window.User.Apellido, style: 'footer', alignment: 'left', margin: [40,10,0,0]},
                                {text: 'Pág. ' + currentPage.toString() + ' de ' + pageCount, style: 'footer', alignment: 'right', margin: [0,10,40,0]}
                            ]
                        };
                    }
                }
            },
            generate() {
                this.makeDesagregadoReport();
                pdfMake.createPdf(this.docDefinition).open();
            },
            makeTableBody() {
                this.tracingData.forEach((wrapper) => {
                    if(wrapper.indicadores.length > 0){
                        this.bodyTable.push(['Objetivo: ' + wrapper.nombreObjetivo]);
                        this.bodyTable.push(['Resultado: ' + wrapper.nombreResultado]);
                        wrapper.indicadores.forEach((ind) => {
                            this.bodyTable.push(
                                [
                                    {
                                        table: {
                                            body: [
                                                ['Indicador','Nivel','Desagregados','Resultados ' + (parseInt(this.optionPanelProperties.year,10) - 1), 'Q t1', 'Q t2', 'Q t3', 'Q t4', 'Total ' +  this.optionPanelProperties.year, 'Meta ' + this.optionPanelProperties.year,'Frecuencia','Metodologia de recolección de datos', 'Organizaciones responsables', 'Fuente de datos'],
                                                [ind.nombreIndicador, this.getArrayAsList(ind.niveles), ind.listaDesagregados, this.numberWithCommas(ind.totalAnterior), this.numberWithCommas(ind.totalQ1), this.numberWithCommas(ind.totalQ2), this.numberWithCommas(ind.totalQ3), this.numberWithCommas(ind.totalQ4), this.numberWithCommas(ind.totalQ1 + ind.totalQ2 + ind.totalQ3 + ind.totalQ4), this.numberWithCommas(ind.meta), this.getArrayAsList(ind.frecuencias), this.getArrayAsList(ind.metodologias), ind.listaOrganizaciones, this.getArrayAsList(ind.fuentes)]
                                                ]
                                        },
                                    }
                                ],
                            );
                        });
                    }
                });
            },
            getCurrentDate(){
                const today = new Date();
                return today.getDate() + '/' + (today.getMonth() + 1) + '/' + today.getFullYear();
            },
            numberWithCommas(x) {
                if(x === null || x === undefined){
                    return 0;
                }
                return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            },
            getArrayAsList(arr) {
                if (arr === null || arr === undefined){
                    return 'Vácio';
                }
                return arr.join();
            }
        },
        created() {
        }
    }
</script>

