<template>
    <v-tooltip bottom>
        <template v-slot:activator="{ on }">
            <v-btn icon v-on="on" @click="generate"><v-icon color="red">mdi-file-pdf</v-icon></v-btn>
        </template>
        <span>Generar PDF</span>
    </v-tooltip>
</template>

<script>
    import {mapState} from 'vuex'
    let pdfMake = require("pdfmake/build/pdfmake.js");
    let pdfFonts = require("pdfmake/build/vfs_fonts.js");
    pdfMake.vfs = pdfFonts.pdfMake.vfs;
    export default {
        name: "SIDPDFMaker",
        props: [

        ],
        data() {
            return {
                today: new Date(),
                docDefinition: null,
                pageSize: 'LETTER',
                pageOrientation: 'landscape',
                pageMargins: [ 40, 100, 40, 60 ],
                styles: {
                    title: {fontSize: 20, bold: true},
                    subtitle: {bold: true, fontSize: 16},
                    bodyText: {fontSize: 12},
                    table: {fontSize: 8},
                    footer: {fontSize: 10, italics: true, color: 'gray'}
                },
                icon: 'data:image/png;base64,',
                bodyTable: [],
                codigosPaises: null,
                codigosSocios: null
            };
        },
        computed: {
          ...mapState(['services','tracingData'])
        },
        methods: {
            makeDesagregadoReport() {
                this.makeTableBody();
                this.docDefinition = {
                    pageSize: this.pageSize,
                    pageOrientation: this.pageOrientation,
                    pageMargins: this.pageMargins,
                    styles: this.styles,
                    header: [
                        {
                            columns: [
                                {stack: [
                                        'FUNDACIÓN PANAMERICANA PARA EL DESARROLLO',
                                        {text: 'Reporte de Desagregados', style: 'subtitle'},
                                        {text: 'Fecha: ' + this.getCurrentDate(), style: 'bodyText'},
                                    ],
                                    style: 'title', margin: [ 40, 30, 0, 0 ]},
                                {image: this.icon, width: 140, height: 50, alignment: 'right', margin: [ 0, 30, 20, 30 ]},
                            ],
                        },
                    ],
                    content: [
                        {
                            style: 'table',
                            table: {
                                widths: [ '*' ],
                                body: this.bodyTable
                            }
                        },
                    ],
                    footer: (currentPage, pageCount) => {
                        return {
                            columns: [
                                {text: 'Reporte generado por: ' + window.User.Nombre + ' ' + window.User.Apellido, style: 'footer', alignment: 'left', margin: [40,10,0,0]},
                                {text: 'Pág. ' + currentPage.toString() + ' de ' + pageCount, style: 'footer', alignment: 'right', margin: [0,10,40,0]}
                            ]
                        };
                    }
                }
            },
            generate() {
                this.makeDesagregadoReport();
                pdfMake.createPdf(this.docDefinition).open();
            },
            makeTableBody() {
                this.tracingData.forEach((wrapper) => {
                    if(wrapper.indicadores.length > 0){
                        this.bodyTable.push(['Objetivo: ' + wrapper.nombreObjetivo]);
                        this.bodyTable.push(['Resultado: ' + wrapper.nombreResultado]);
                        wrapper.indicadores.forEach((ind) => {
                            this.bodyTable.push(
                                [
                                    {
                                        table: {
                                            body: [
                                                ['Indicador','Organizaciones Responsables','Desagregados','Países','Socios Internacionales','Total'],
                                                [ind.nombreIndicador, ind.listaOrganizaciones,[
                                                    {
                                                        table: {
                                                            width: ['auto'],
                                                            body: this.getDesagregadoList(ind.desagregados)
                                                        }
                                                    }
                                                ],[
                                                    {
                                                        table: {
                                                            body: this.getCountryBody(ind.desagregados, ind.registroSocios)
                                                        },
                                                    }
                                                ], [
                                                    {
                                                        table: {
                                                            body: this.getSocioBody(ind.desagregados, ind.registroSocios)
                                                        },
                                                    }
                                                ], [
                                                    {
                                                        table: {
                                                            body: this.getTotal(ind.desagregados, ind.registroSocios)
                                                        },
                                                    }
                                                ]],
                                            ]
                                        },
                                    }
                                ],
                            );
                        });
                    }
                });
            },
            getCurrentDate(){
                const today = new Date();
                return today.getDate() + '/' + (today.getMonth() + 1) + '/' + today.getFullYear();
            },
            getCountryHeaders() {
                return this.codigosPaises.map(function (c) {
                    return c.nombre;
                })
            },
            getSocioHeaders() {
                return this.codigosSocios.map(function (s) {
                    return s.nombre;
                })
            },
            getDesagregadoList(des) {
                let tableRows = [['-']];
                tableRows = tableRows.concat(
                    des.map((row) => {
                        return [row.nombre]
                    })
                );
                return tableRows;
            },
            getCountryBody(des, reg) {
                let body = [this.getCountryHeaders()];
                body = body.concat(
                    des.map((row) => {
                        return this.codigosPaises.map((c) => {
                            let result = 0;
                            reg.forEach((r) => {
                                if (r.codigo === c.nombre && r.idDesagregado === row.id)
                                    result += r.valor;
                            });
                            return this.numberWithCommas(result);
                        })
                    })
                );
                return body;
            },
            getSocioBody(des, reg) {
                let body = [this.getSocioHeaders()];
                body = body.concat(
                    des.map((row) => {
                        return this.codigosSocios.map((s) => {
                            let result = 0;
                            reg.forEach((r) => {
                                if (r.id === s.id && r.idDesagregado === row.id)
                                    result += r.valor;
                            });
                            return this.numberWithCommas(result);
                        })
                    })
                );
                return body;
            },
            getTotal(des, reg) {
                let body = [['-']];
                body = body.concat(
                    des.map((row) => {
                        let result = 0;
                        reg.forEach((r) => {
                            if (r.idDesagregado === row.id)
                                result += r.valor;
                        });
                        return [this.numberWithCommas(result)];
                    })
                );
                return body;
            },
            numberWithCommas(x) {
                if(x === null || x === undefined){
                    return 0;
                }
                return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            }
        },
        created() {
            this.services.simpleIdentificadorService.getCodigoPaises()
                .then(r => {
                    this.codigosPaises = r.data;
                })
                .catch(e => {
                    this.showInfo(e.toString());
                });
            this.services.simpleIdentificadorService.getCodigoSocios()
                .then(r => {
                    this.codigosSocios = r.data;
                })
                .catch(e => {
                    this.showInfo(e.toString());
                });
        }
    }
</script>

