<template>
    <v-tooltip bottom>
        <template v-slot:activator="{ on }">
            <v-btn icon v-on="on" @click="generate"><v-icon color="red">mdi-file-pdf</v-icon></v-btn>
        </template>
        <span>Generar PDF</span>
    </v-tooltip>
</template>

<script>
    import {mapState} from 'vuex'
    let pdfMake = require("pdfmake/build/pdfmake.js");
    let pdfFonts = require("pdfmake/build/vfs_fonts.js");
    pdfMake.vfs = pdfFonts.pdfMake.vfs;
    export default {
        name: "AuditoriaPDFMaker",
        data() {
            return {
                today: new Date(),
                docDefinition: null,
                pageSize: 'LETTER',
                pageOrientation: 'portrait',
                pageMargins: [ 40, 120, 40, 60 ],
                styles: {
                    title: {fontSize: 20, bold: true},
                    subtitle: {bold: true, fontSize: 16},
                    bodyText: {fontSize: 12},
                    table: {fontSize: 8},
                    footer: {fontSize: 10, italics: true, color: 'gray'}
                },
                bodyTable: [],
                icon: 'data:image/png;base64,'
            }
        },
        computed: {
          ...mapState(['modelSpecification','dataTable'])
        },
        methods: {
            makeReport() {
                this.makeReportBody();
                this.docDefinition = {
                    pageSize: this.pageSize,
                    pageOrientation: this.pageOrientation,
                    pageMargins: this.pageMargins,
                    styles: this.styles,
                    header: [
                        {
                            columns: [
                                {stack: [
                                        'FUNDACIÓN PANAMERICANA PARA EL DESARROLLO',
                                        {text: 'Reporte de Auditoria', style: 'subtitle'},
                                        {text: 'Fecha: ' + this.getCurrentDate(), style: 'bodyText'},
                                    ],
                                    style: 'title', margin: [ 40, 30, 0, 0 ]},
                                {image: this.icon, width: 140, height: 50, alignment: 'right', margin: [ 0, 30, 20, 30 ]},
                            ],
                        },
                    ],
                    content: [
                        {
                            style: 'table',
                            table: {
                                headerRows: 1,
                                widths: [ '50%','25%','25%' ],
                                body: this.bodyTable
                            }
                        },
                    ],
                    footer: (currentPage, pageCount) => {
                        return {
                            columns: [
                                {text: 'Reporte generado por: ' + window.User.Nombre + ' ' + window.User.Apellido, style: 'footer', alignment: 'left', margin: [40,10,0,0]},
                                {text: 'Pág. ' + currentPage.toString() + ' de ' + pageCount, style: 'footer', alignment: 'right', margin: [0,10,40,0]}
                            ]
                        };
                    }
                }
            },
            makeReportBody() {
                this.bodyTable.push(['Acción Realizada','Por Usuario','Fecha y Hora']);
                this.dataTable.forEach(r => {
                    this.bodyTable.push([r['summary'],r.byUser,r.auditAt])
                })
            },
            generate() {
                this.makeReport();
                pdfMake.createPdf(this.docDefinition).open();
            },
            getCurrentDate(){
                const today = new Date();
                return today.getDate() + '/' + (today.getMonth() + 1) + '/' + today.getFullYear();
            },
        },
        created() {

        }
    }
</script>

